import React from "react";
import Fade from "react-reveal/Fade";
import ReactHtmlParser, {
  processNodes,
  convertNodeToElement,
  htmlparser2
} from "react-html-parser";

class Project extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      prevSlide: 0,
      activeSlide: 0
    };
  }

  changeSlides(change) {
    const prevSlide = this.state.activeSlide;
    let activeSlide = prevSlide + change;
    if (activeSlide < 0) activeSlide = slides.length - 1;
    if (activeSlide >= slides.length) activeSlide = 0;
    this.setState({ activeSlide, prevSlide });
  }

  render() {
    const item = this.state.activeSlide;

    return (
      <div>
        <div className="arrow-box">
          <div onClick={() => this.changeSlides(-1)} className="arrow-up" />
          <div className="mid" />
          <div onClick={() => this.changeSlides(1)} className="arrow-down" />
        </div>
        <div className="project-wrapper">
          <Fade top>
            <div className="project-detail">
              <h2 className="number">{"0" + (item + 1)}</h2>
              <div className="detail-wrapper">
                <h2 className="title">{slides[item].title}</h2>
                <p className="description">
                  {ReactHtmlParser(slides[item].description)}
                  <a
                    target="_blank"
                    rel="noreferrer noopener"
                    href={slides[item].link}
                    className="arrow-button"
                  ></a>
                </p>
              </div>
            </div>
          </Fade>
          <Fade bottom>
            <div className="image-wrapper">
              <div
                className="project-image"
                style={{
                  backgroundImage: "url(" + slides[item].img + ")"
                }}
              />
            </div>
          </Fade>
        </div>
      </div>
    );
  }
}

const slides = [
  {
    title: "Weather Finder",
    description:
      "Weather finder practice in React. Without any existing weather related packages. Weather data fatch CWB Opendata API. <br><br> DEVELOPER / TZU-HUA WANG <br> DATE / OCT. 2019",
    img:
      "https://cdn.discordapp.com/attachments/573403066271793152/644467489702543370/weather.png",
    link: "https://github.com/marukohua/weatherfinder"
  },
  {
    title: "ESAPPDiY",
    description:
      "Use Yii PHP framework for web development. <br><br> DEVELOPER / TZU-HUA WANG & SHIN-YU WU & HOU-WEI SU & KUAN-YUAN CHEN <br> DATE / 2016",
    img:
      "https://cdn.discordapp.com/attachments/573403066271793152/644436065163411458/esappdiy.png",
    link: "http://www.u-sync.com/site/service"
  },
  {
    title: "Daily UI",
    description:
      "100 days Daily UI in React Challenge. <br><br> DEVELOPER / TZU-HUA WANG <br> START / NOV. 2019",
    img:
      "https://media.discordapp.net/attachments/573403066271793152/644474505107996672/03.png?width=676&height=676",
    link: "https://codepen.io/collection/XpWxbN/"
  },
  {
    title: "發電廠在哪裡",
    description:
      "「發電廠在哪裡」是一款以能源為主題的桌遊。 <br><br> GAME DESIGN / TZU-HUA WANG & KUAN-LING HUANG & SHANG-JUNG WU <br> ART DESIGN & ILLUSTRATION / TZU-HUA WANG & KUAN-LING HUANG <br> DATE：DEC. 2017",
    img:
      "https://media.discordapp.net/attachments/573403066271793152/644472507306475530/4.png?width=676&height=676",
    link: "https://www.behance.net/gallery/85233813/_"
  }
];

export default Project;
